import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { Router, ActivatedRoute } from '@angular/router';
import { locale as spanish } from './i18n/es';
import { SendConfirmationService } from './send-confirmation.service';

@Component({
    selector   : 'send-confirmation',
    templateUrl: './send-confirmation.component.html',
    styleUrls  : ['./send-confirmation.component.scss']
})
export class SendConfirmationComponent implements OnInit
{
    
    error: boolean;
    fetch: boolean = false;

    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private _fuseConfigService: FuseConfigService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private confirmService: SendConfirmationService
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);
    }

    ngOnInit() {
        this.activatedRoute.queryParamMap.subscribe((res: any) => {
            if(res.params.param1) {
                this.confirmService.checkVerifCode(res.params.param1).subscribe(res => {
                    this.fetch = true;
                    this.error = false;
                    this.router.navigate(['.'], { relativeTo: this.activatedRoute, queryParams: null });
                }, err => {
                    this.fetch = true;
                    this.error = true;
                })
            } else {
                // this.router.navigate(['auth/login']);
            }
        })
    }
}
